import Image from "next/image";
import Link from "next/link";
import Navbar from "@workspace/ui/sections/Navbar";
import Footer from "@workspace/ui/sections/Footer";
import { 
  ArrowRight, 
  Building2, 
  ShoppingCart, 
  Landmark, 
  LayoutGrid,
  Zap,
  Globe,
  MonitorSmartphone
} from "lucide-react";

const apps = [
  {
    title: "Company Profile",
    description: "Modern landing page for NexaTech IT Solutions with complete services and portfolio sections.",
    icon: Building2,
    href: "http://localhost:3001",
    color: "from-blue-600/20 via-indigo-600/10 to-transparent",
    borderColor: "border-blue-500/30",
    iconColor: "text-blue-500",
    tag: "Business"
  },
  {
    title: "Ecommerce",
    description: "Premium UrbanStyle fashion store with product catalog, flash sales, and cart functionality.",
    icon: ShoppingCart,
    href: "http://localhost:3002",
    color: "from-amber-600/20 via-orange-600/10 to-transparent",
    borderColor: "border-amber-500/30",
    iconColor: "text-amber-500",
    tag: "Retail"
  },
  {
    title: "Government Portal",
    description: "Official smart city portal with public services, news, stats, and animated statistics.",
    icon: Landmark,
    href: "http://localhost:3003",
    color: "from-emerald-600/20 via-teal-600/10 to-transparent",
    borderColor: "border-emerald-500/30",
    iconColor: "text-emerald-500",
    tag: "Public Service"
  },
  {
    title: "Product Catalog",
    description: "Detailed product showcasing with filtering, brand stories, and category exploration.",
    icon: LayoutGrid,
    href: "http://localhost:3004",
    color: "from-purple-600/20 via-pink-600/10 to-transparent",
    borderColor: "border-purple-500/30",
    iconColor: "text-purple-500",
    tag: "Showcase"
  }
];

export default function PortalPage() {
  return (
    <div className="min-h-screen bg-black font-sans text-white selection:bg-indigo-500/30">
      <Navbar />
      
      <main className="relative pt-16">
        {/* Background Decorations */}
        <div className="absolute inset-0 overflow-hidden pointer-events-none">
          <div className="absolute top-0 right-0 h-[500px] w-[500px] rounded-full bg-indigo-600/10 blur-[120px]" />
          <div className="absolute bottom-0 left-0 h-[500px] w-[500px] rounded-full bg-purple-600/10 blur-[120px]" />
          <div className="absolute inset-x-0 h-px bg-gradient-to-r from-transparent via-white/10 to-transparent top-0" />
        </div>

        {/* Hero Section */}
        <section className="relative px-6 py-20 sm:py-32 lg:px-8">
          <div className="mx-auto max-w-7xl text-center">
            <div className="mb-8 flex justify-center">
              <div className="relative rounded-full px-3 py-1 text-sm leading-6 text-zinc-400 ring-1 ring-white/10 hover:ring-white/20 transition-all bg-white/5 backdrop-blur-sm">
                <span className="flex items-center gap-2">
                  <Zap className="h-4 w-4 text-indigo-500 fill-indigo-500" />
                  Monorepo Infrastructure Active
                </span>
              </div>
            </div>
            
            <h1 className="text-4xl font-bold tracking-tight sm:text-7xl bg-gradient-to-b from-white to-zinc-500 bg-clip-text text-transparent">
              Workspaces Hub
            </h1>
            <p className="mt-6 text-lg leading-8 text-zinc-400 max-w-2xl mx-auto">
              Welcome to the central portal. This monorepo architecture houses multiple specialized applications, 
              sharing a unified design system and core libraries.
            </p>
          </div>
        </section>

        {/* Apps Grid */}
        <section className="relative px-6 pb-32 lg:px-8">
          <div className="mx-auto max-w-7xl">
            <div className="grid gap-8 sm:grid-cols-2 lg:grid-cols-4">
              {apps.map((app, idx) => (
                <Link 
                  key={app.title}
                  href={app.href}
                  target="_blank"
                  className={`group relative flex flex-col items-start rounded-3xl border ${app.borderColor} bg-zinc-900/50 p-8 transition-all duration-300 hover:bg-zinc-900 hover:scale-[1.02] hover:shadow-2xl hover:shadow-${app.iconColor.split('-')[1]}-500/10 backdrop-blur-sm overflow-hidden`}
                >
                  {/* Hover Gradient Overlay */}
                  <div className={`absolute inset-0 bg-gradient-to-br ${app.color} opacity-0 group-hover:opacity-100 transition-opacity duration-500`} />
                  
                  <div className={`relative mb-6 rounded-2xl bg-zinc-800 p-4 ${app.iconColor} ring-1 ring-white/5 group-hover:ring-white/10 transition-all`}>
                    <app.icon className="h-6 w-6" />
                  </div>
                  
                  <div className="relative mb-4 flex items-center gap-2">
                    <span className="text-xs font-semibold uppercase tracking-wider text-indigo-400">
                      {app.tag}
                    </span>
                  </div>

                  <h3 className="relative text-xl font-bold text-white mb-2 group-hover:text-white transition-colors">
                    {app.title}
                  </h3>
                  
                  <p className="relative text-sm leading-relaxed text-zinc-400 group-hover:text-zinc-300 transition-colors">
                    {app.description}
                  </p>

                  <div className="relative mt-8 flex items-center gap-2 text-sm font-medium text-white opacity-0 group-hover:opacity-100 transition-all translate-x-[-10px] group-hover:translate-x-0">
                    Visit Application <ArrowRight className="h-4 w-4" />
                  </div>
                </Link>
              ))}
            </div>

            {/* Feature Highlights */}
            <div className="mt-32 grid gap-12 lg:grid-cols-3 border-t border-white/5 pt-16">
              {[
                {
                  icon: Globe,
                  title: "Shared UI Package",
                  desc: "All apps consume components from `@workspace/ui`, ensuring perfect brand consistency."
                },
                {
                  icon: MonitorSmartphone,
                  title: "Responsive Core",
                  desc: "Built with a mobile-first approach, every portal application works flawlessly on any device."
                },
                {
                  icon: Zap,
                  title: "Turbo-Powered",
                  desc: "Turborepo orchestrates builds and dev servers with blazing fast caching and parallel execution."
                }
              ].map((item) => (
                <div key={item.title} className="flex flex-col items-center text-center">
                  <div className="mb-4 rounded-full bg-white/5 p-3 text-indigo-500 ring-1 ring-white/10">
                    <item.icon className="h-5 w-5" />
                  </div>
                  <h4 className="text-lg font-semibold text-white mb-2">{item.title}</h4>
                  <p className="text-sm text-zinc-500 leading-relaxed">{item.desc}</p>
                </div>
              ))}
            </div>
          </div>
        </section>
      </main>

      <Footer />
    </div>
  );
}
